.toast-completed.open .toast-completed__wraper,
.toast-completed.open .toast-completed__window {
  opacity: 1;
  z-index: 1000;
  transition: all 0.5s ease;
}

.toast-completed {
  &__wraper {
    opacity: 0;
    z-index: -1;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(194, 115, 115, 0.3);
  }
  &__window {
    opacity: 0;
    z-index: -1;
    max-width: 530px;
    border-radius: 30px;
    background: white;
    margin: 50px auto;
  }
  &__heading {
    display: flex;
    justify-content: space-between;
    align-self: flex-end;
    padding: 5px 10px;
    .heading-title {
      display: flex;
      justify-content: center;
      padding: 32px;
      font-size: 24px;
      font-weight: 700;
    }
    .heading-btn--close {
      font-size: 24px;
      font-weight: 700;
      color: #000;
      text-shadow: 0 1px 0 #fff;
      margin-right: 12px;
      height: 32px;
      opacity: 0.3;
    }
  }
}
